.list{
    background-color: yellow;
    color: black;
    width: 300px;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 10px;
    position: relative;
    font-size: 20px;

}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons{
    margin: 0 5px;
    font-size: 20px;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: black;
    font-size: 20px;;
    outline: none;
}
