body{
  background-color: #9266f2;
}

.App{
    background-color: #3f3160;
    min-height: 600px;
    width: 500px;
    margin: 50px auto;
}

#todo-form input{
  background-color: rgb(27, 112, 137);
  width: 300px;
  height:60px;
  padding: 0 10px;
  
  font-size: 20px;
  border-radius: 10px;
  color: white;



}

#todo-form input, #todo-form button{
  margin-top: 20px;
  margin-left: 20px;
  border-radius: 10px;
  border: 0;
}

#todo-form input::placeholder{
  color:white;
  text-align: center;
}
#todo-form input:focus, #todo-form button:focus{
  outline: none;
}


#todo-form button{
   width: 100px;
   height: 60px;;
   cursor: pointer;
   font-weight: bolder;

}

